export const CustomerDetailsPage = () => {
  const [customer, setCustomer] = useState(undefined as any);
  const [employeeList, setEmployeeList] = useState([] as ListItem[]);
  const [accountTeamList, setAccountTeamList] = useState([] as ListItem[]);
  const [competitorsList, setCompetitorsList] = useState([] as ListItem[]);
  const [suppliersList, setSuppliersList] = useState([] as ListItem[]);
  const [productsList, setProductsList] = useState([] as ListItem[]);
  const [salesHierarchy, setSalesHierarchy] = useState([] as TreeNodeType[]);

  useEffect(() => {
    setCustomer({ ...TEST_DATA });
    setEmployeeList(TEST_EMPLOYEE_LIST);
    setAccountTeamList(TEST_ACCOUNT_TEAN);
    setSuppliersList(TEST_SUPPLIERS);
    setCompetitorsList(TEST_COMPETITORS);
    setProductsList(TEST_PRODUCTS);
    setSalesHierarchy(TEST_SALES_HIERARCHY);
  }, []);

  return (
    <div className={s.container}>
      {customer && <div className={s.panel}>
        <Panel classNames={classnames(s.main, s.panel)}>
          <div className={s.main_header}>
            <div className={s.main_logo_container}>
              <img src={customer.logo} alt='' className={s.main_logo} />
            </div>
            <div className={s.main_header_info}>
              <p>{customer.partyType === ORGANIZATION ? customer.organizationName : customer.firstGivenName}</p>
              <Tag type='blue' fill={true} title={customer.partyType === ORGANIZATION ? 'Organization' : 'Person'} />
            </div>
              <Button title='Viewing' type='blue' onClick={() => {}} iconOnStart={<span className={s.viewing_icon} />} iconOnEnd={<span className={s.viewing_arrow_icon} />}/>
          </div>
          <div className={s.columns}>
            <div>
              <p className={s.main_subheader}>Key Financial Figures</p>
              <div className={classnames(s.main_info, s.large_margin)}>
                <p><span className={s.main_info_title}>Revenue: </span>{customer.revenue}</p>
                <p><span className={s.main_info_title}>Locations: </span>{customer.locations}</p>
                <p><span className={s.main_info_title}>Net income: </span>{customer.netIncome}</p>
              </div>
              <div className={s.main_info}>
                <p><span className={s.main_info_title}>Auto-generated Sequencial: </span>{customer.autoGeneratedSequencial}</p>
                <p><span className={s.main_info_title}>Name: </span>{customer.name}</p>
                <p><span className={s.main_info_title}>EntityURI: </span>{customer.entityUri}</p>
                <p><span className={s.main_info_title}>Doing Business As Name: </span>{customer.businessName}</p>
                <p><span className={s.main_info_title}>Website URL: </span>{customer.websiteUrl}</p>
              </div>
              <div className={s.main_info}>
                <p><span className={s.main_info_title}>Is Subsidiary: </span>{customer.isSubsidiary}</p>
                <p><span className={s.main_info_title}>DUNS Number: </span>{customer.duns}</p>
              </div>
              <div>
                <p><span className={s.main_info_title}>Company Type: </span>{customer.companyType}</p>
                <p><span className={s.main_info_title}>Ticker: </span>{customer.ticker}</p>
              </div>
            </div>
            <div>
              <p className={s.main_subheader}>Address</p>
              <div className={s.main_info_highlighted}>
                <p>{`${customer.postalCode} ${customer.cityName}, ${customer.stateProvince}, ${customer.countryName}`}</p>
                <p>{customer.addressLine1}</p>
              </div>
              <p className={s.main_subheader}>Phone</p>
              <div className={s.main_info_highlighted}>
                <p>{customer.phone}</p>
                <p>{customer.phoneFaxNumber}</p>
              </div>
            </div>
          </div>
        </Panel>
        <div className={s.panel}>
          <Panel classNames={s.panel}>
            <ListComponent
              title="Employees"
              list={employeeList}
              showTotal={true}
              actions={[]}
              info={<div></div>}
              itemSize="medium"
              maxVisibleItems={5}
            />
          </Panel>
          <Panel classNames={s.panel}>
            <ListComponent
              title="Account Team"
              list={accountTeamList}
              showTotal={true}
              actions={[]}
              info={<div></div>}
              itemSize="medium"
              maxVisibleItems={5}
            />
          </Panel>
        </div>
        <div className={s.panel}>
          <Panel classNames={s.panel}>
            <ListComponent
              title="Competitors"
              list={competitorsList}
              showTotal={true}
              actions={[]}
              info={<div></div>}
              itemSize="small"
              maxVisibleItems={6}
            />
          </Panel>
          <Panel classNames={s.panel}>
            <ListComponent
              title="Suppliers"
              list={suppliersList}
              showTotal={true}
              actions={[]}
              info={<div></div>}
              itemSize="small"
              maxVisibleItems={6}
            />
          </Panel>
        </div>
      </div>}
      {customer && <div>
        <div>
          <div>
            {customer.potencialMatches && customer.potencialMatches.length > 0 && <Panel>
              <ListTitle title='Potencial Matches' />
              <div className={s.potencial_matches_container}>
                <div className={s.potencial_matches_content}>
                  <img src={customer.potencialMatches[0].logo} alt='' className={s.potencial_matches_logo} />
                  <div className={s.potencial_matches_info}>
                    <p>{customer.potencialMatches[0].name}</p>
                    <Tag title={customer.potencialMatches[0].type} type='blue' fill={true} />
                  </div>
                </div>
                <Button title='Company Details' type='blue' onClick={() => {}} />
                <Button title='More Matches' type='blue' onClick={() => {}} />
              </div>
            </Panel>}
            <Panel>
              <ListTitle title='Key Financial Figures Overview'/>
            </Panel>
          </div>
          <Panel>
            <ListTitle title='History'/>
          </Panel>
        </div>
        <div>
          <Panel>
            <ListTitle title='Sales Hierarchy' />
            <SimpleTree nodes={salesHierarchy} showTotal={true} />
          </Panel>
          <Panel>
            <ListTitle title='DnB Hierarchy' />
            <SimpleTree nodes={salesHierarchy}  showTotal={true} />
          </Panel>
        </div>
        <Panel>
          <ListComponent
            title="Products"
            list={productsList}
            showTotal={true}
            actions={[]}
            info={<div></div>}
            itemSize="small"
            maxVisibleItems={6}
          />
        </Panel>
      </div>}
    </div>
  );
};
